@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&family=Source+Code+Pro&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", "sans-serif";
  overflow-x: hidden;
  scroll-behavior: smooth;
}
*::-webkit-scrollbar {
  width: 10px;
  background: #002;
}
*::-webkit-scrollbar-thumb {
  background: #030;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  border: 1.5px solid #002;
}
*::-webkit-scrollbar-thumb:hover {
  background: #756;
}
* .code {
  outline: none;
}
* .comment,
* .code * {
  font-family: "Source Code Pro", consolas;
}
* hr {
  opacity: 0.05;
}

section {
  position: fixed;
  top: 50px;
  left: 0;
  height: calc(100vh - 50px);
  width: 100vw;
  overflow-y: scroll;
  background-color: #222;
}
section .section {
  min-height: 100%;
  width: 100vw;
  background: #464675;
  overflow: visible;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 50px;
  width: 100vw;
  background: #111;
  color: cornsilk;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 25px;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  transition: 0.2s;
}
.nav:hover {
  color: aqua;
}
.nav .logo {
  height: 30px;
  width: 30px;
  background: url(../favicon.ico);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  transition: 0.1s linear;
}
.nav .logo:hover {
  transform: scale(1.5);
}
.nav .login_link_nav {
  color: cornsilk;
  text-decoration: none;
  font-size: 20px;
}
.nav .login_link_nav:hover {
  color: aqua;
}
.nav .open_menu {
  color: cornsilk;
  height: 40px;
  width: 40px;
  text-decoration: none;
  font-size: 18px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  transition: 0.2s;
}
.nav .open_menu:hover {
  background: rgba(255, 248, 220, 0.172);
  color: aqua;
}

.sidebar {
  position: fixed;
  top: 50px;
  left: 10px;
  height: calc(100vh - 50px);
  width: 25vw;
  background-color: #08142c;
  transition: 0.2s linear;
  z-index: 10000;
  box-shadow: 0 10px 20px #000, 0 10px 22px #001, 0 10px 24px #002, 0 10px 26px #003, 0 10px 28px #222;
}
.sidebar .sideLogo {
  position: relative;
  height: 35%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3137254902);
}
.sidebar .sideLogo::before {
  content: "";
  position: absolute;
  top: calc(50% - 10vh);
  left: calc(50% - 10vh);
  height: 20vh;
  width: 20vh;
  background: url(../favicon.ico);
  border-radius: 50%;
  background-size: 100% 100%;
  background-position: center;
}
.sidebar .sideNavs {
  height: calc(0.65 * (100vh - 50px));
  width: 100%;
}
.sidebar .sideNavs .Sidenav {
  height: 25%;
  width: 100%;
  background: transparent;
  color: #a6a6a6;
  font-size: 22px;
  display: flex;
  align-items: center;
  padding: 0 22px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3137254902);
  transition: 0.1s;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  text-decoration: none;
}
.sidebar .sideNavs .Sidenav:first-child {
  border-top: 1px solid #08142c;
}
.sidebar .sideNavs .Sidenav:hover {
  background-color: rgba(0, 0, 0, 0.137254902);
}

