@media screen and (max-width: 850px) {
  .nav .login_link_nav {
    display: none;
  }
  .sidebar {
    width: 50vw;
    z-index: 1000;
    top: 0;
    left: -50vw;
    transition: 0.4s linear;
  }
  .sidebar .sideLogo {
    height: 25%;
  }
  .sidebar .sideLogo::before {
    top: calc(50% - 7.5vh);
    left: calc(50% - 7.5vh);
    height: 15vh;
    width: 15vh;
  }
  .sidebar .sideNavs {
    height: 75vh;
  }
  .sidebar .sideNavs .Sidenav {
    height: 18;
  }
  section {
    left: 0;
    width: 100vw;
  }
  section .section {
    width: 100vw;
  }
  .section.s_h .Shower {
    flex-direction: column;
    justify-content: space-around;
    width: 90%;
  }
  .section.s_h .Shower .head,
  .section.s_h .Shower .para {
    width: 80%;
    text-align: center;
  }
  .section.s_h .Shower .para {
    text-align: center;
    font-size: 18px;
    transform: translateY(-10vh);
  }
  .section.s_h .Shower .para .head_links .headlinks {
    font-size: 15px;
    padding: 12px 18px;
  }
  #footer h1,
  #footer p,
  #footer .social_media {
    width: 100%;
  }
  .section.about .component_head.long {
    font-size: 25px;
    line-height: 5vh;
    padding: 2vh;
  }
  .section.tut .content {
    display: block;
    flex-direction: column;
  }
  .section.tut .content .plintro {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
  }
  .section.about.doc.editor_holder .content .editor_area {
    flex-direction: column;
  }
  .section.about.doc.editor_holder .content .editor_area .editor_text,
  .section.about.doc.editor_holder .content .editor_area .output {
    width: 99%;
    margin: 10px;
  }
}
@media (max-width: 700px) {
  .nav {
    padding: 0 5px;
  }
}
@media (max-width: 500px) {
  .section.login .login_card {
    height: 70vh;
    width: 85%;
    overflow-y: none;
  }
  .section.login .login_card::-webkit-scrollbar {
    display: none;
  }
  .sidebar {
    top: 50px;
    width: 100vw;
    height: calc(100vh - 50px);
    transition: 0.2s linear;
  }
  .sidebar .sideNavs {
    height: calc(0.75 * (100vh - 50px));
  }
}
@media (max-width: 500px) and (max-height: 750px) {
  .section.login .login_card {
    height: 90vh;
  }
