// @import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&family=Source+Code+Pro&display=swap');

$SideHeight : calc(100vh - 50px);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto','sans-serif';
    overflow-x: hidden;
    scroll-behavior: smooth;

    &::-webkit-scrollbar {
        width: 10px;
        background: #002;
    }

    &::-webkit-scrollbar-thumb {
        background: #030;
        transition: .2s linear;
        border: 1.5px solid #002;

        &:hover {
            background: #756;
        }
    }

    .code {
        outline: none;
    }

    .comment,
    .code * {
        font-family: 'Source Code Pro', consolas;
    }

    hr {
        opacity: .05;
    }
}

section {
    position: fixed;
    top: 50px;
    left: 0;
    height: calc(100vh - 50px);
    width: 100vw;
    overflow-y: scroll;
    background-color: #222;

    .section {
        min-height: 100%;
        width: 100vw;
        background: #464675;
        overflow: visible;
    }
}

.nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 50px;
    width: 100vw;
    background: #111;
    color: cornsilk;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px;
    font-weight: bold;
    user-select: none;
    transition: .2s;

    &:hover {
        color: aqua;
    }

    .logo {
        height: 30px;
        width: 30px;
        background: url(../favicon.ico);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        transition: .1s linear;

        &:hover {
            transform: scale(1.5);
        }
    }

    .login_link_nav {
        color: cornsilk;
        text-decoration: none;
        font-size: 20px;

        &:hover {
            color: aqua;
        }
    }

    .open_menu {
        color: cornsilk;
        height: 40px;
        width: 40px;
        text-decoration: none;
        font-size: 18px;
        border-radius: 50%;
        display: grid;
        place-items: center;
        transition: .2s;

        &:hover {
            background: rgba(255, 248, 220, 0.172);
            color: aqua;
        }
    }
}

.sidebar {
    position: fixed;
    top: 50px;
    left: 10px;
    height: $SideHeight;
    width: 25vw;
    background-color: #08142c;
    transition: .2s linear;
    z-index: 10000;
    box-shadow: 0 10px 20px #000,
        0 10px 22px #001,
        0 10px 24px #002,
        0 10px 26px #003,
        0 10px 28px #222;

    .sideLogo {
        position: relative;
        height: 35%;
        width: 100%;
        background: #00000050;

        &::before {
            $he: 20vh;
            content: '';
            position: absolute;
            top: calc(50% - $he/2);
            left: calc(50% - $he/2);
            height: $he;
            width: $he;
            background: url(../favicon.ico);
            border-radius: 50%;
            background-size: 100% 100%;
            background-position: center;
        }
    }

    .sideNavs {
        height: calc(65/100 * (100vh - 50px));
        width: 100%;

        .Sidenav {
            height: 25%;
            width: 100%;
            background: transparent;
            color: #a6a6a6;
            font-size: 22px;
            display: flex;
            align-items: center;
            padding: 0 22px;
            border-bottom: 1px solid #00000050;
            transition: .1s;
            user-select: none;
            text-decoration: none;

            &:first-child {
                border-top: 1px solid #08142c
            }

            &:hover {
                background-color: #00000023;
            }

        }

    }

}


.section.s_h {
    background-image: url('../bg.jpg');
    background-color: rgba(3, 0, 51, 0.749);
    background-blend-mode: multiply;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    color: antiquewhite;
    display: grid;
    place-items: center;
    user-select: none;

    .Shower {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 70%;
        width: 85%;

        .head,
        .para {
            width: 50%;
            text-align: center;
        }

        .para {
            text-align: left;

            .head_links {
                height: 5vh;
                overflow: hidden;
                display: flex;
                justify-content: space-evenly;
                align-items: center;

                .headlinks {
                    transition: .2s;
                    display: inline-block;
                    padding: 15px 25px;
                    font-size: 18px;
                    text-decoration: none;
                    color: #000;
                    background: cornsilk;

                    &:hover {
                        background: rgb(205, 181, 135);
                        color: #030;
                        box-shadow: 0 0 15px #0000006d,
                            0 0 25px #ffffff72;
                    }

                }
            }
        }

        .head {
            font-size: 30px;
        }

    }
}

#footer {
    height: 35vh;
    width: 100vw;
    background: #222;
    color: #a2a2a2;
    overflow: visible;
    user-select: none;

    h1 {
        line-height: 7vh;
        text-align: center;
        width: 100%;
        background: #0000003d;
    }

    p {
        text-align: center;
        width: 100%;
        line-height: 15vh;
    }

    .social_media {
        display: flex;
        text-align: center;
        width: 100%;
        line-height: 8vh;
        justify-content: center;
        align-items: center;

        a {
            display: grid;
            color: #666666e7;
            text-decoration: none;
            margin: 0 15px;
            height: 50px;
            width: 50px;
            overflow: visible;
            font-size: 17px;
            padding: 15px 15px;
            border-radius: 50%;
            transition: .15s;
            place-items: center;

            &:hover {
                background: #666666e7;
                color: #222;
            }

        }
    }
}


.section.about {
    background-image: url('../bg.jpg');
    background-color: rgba(3, 0, 51, 0.749);
    background-blend-mode: multiply;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;

    .component_head {
        color: wheat;
        line-height: 20vh;
        font-size: 50px;
        text-transform: uppercase;
        text-align: center;
        border-bottom: .2px solid rgba(255, 248, 220, 0.372);
    }

    .component_head.long {
        font-size: 35px;
    }

    .content {
        color: #8b7a7a;
        width: 90%;
        margin: 50px 5% 0 5%;
        padding: 0 0 50px 0;

        .sub_heading {
            color: #a2a2a2;
        }

        p {
            font-size: 18px;
            line-height: 30px;
        }
    }

    .content.flex {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: wrap;

        .plcards {
            height: 400px;
            width: 300px;
            background: linear-gradient(45deg, #08124c, #08142c, #222, #111);
            margin: 30px 0;
            box-shadow: 0 0 15px #000;

            .image {
                height: 45%;
                background-color: #fff;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                opacity: 0.7;
                transition: .2s;

                &:hover {
                    opacity: 1;
                }
            }

            .image.python {
                background-image: url('../components/python_logo.png');
            }

            .image.js {
                background-size: 100% 100%;
                background-image: url('../components/JavaScript-logo.png');
            }

            .image.cpp {
                background-image: url('../components/cpplogo.jpeg');
            }

            .image.rea {
                background-image: url('../components/react-logo.png');
            }

            .image.ratul {
                background-size: cover;
                background-image: url('../components/ratul.png');
            }

            .image.ratul {
                background-size: cover;
                background-image: url('../components/ratul.png');
            }

            .image.ratul {
                background-size: cover;
                background-image: url('../components/ratul.png');
            }

            .image.ratul {
                background-size: cover;
                background-image: url('../components/ratul.png');
            }

            .image.aodritto {
                background-size: contain;
                background-image: url('../components/aodritto.png');
            }

            .image.aodritto {
                background-size: contain;
                background-image: url('../components/aodritto.png');
            }

            .image.amri {
                background-size: cover;
                background-image: url('../components/amri.png');
            }

            .image.arpan {
                background-size: cover;
                background-image: url('../components/arpan.png');
            }


            .plcontent {
                user-select: none;
                padding: 15px;

                .plhead {
                    color: cornsilk;
                    font-size: 30px;
                    line-height: 40px;
                }

                ul {
                    list-style: circle;
                    display: block;
                    padding: 10px;

                    li {
                        line-height: 25px;
                        font-size: 18px;
                    }
                }

                .go {
                    color: #08124c;
                    background-color: #333;
                    height: 35px;
                    display: block;
                    text-decoration: none;
                    text-transform: uppercase;
                    display: grid;
                    place-items: center;
                    margin-top: 15px;
                    font-weight: bold;
                    transition: .2s;

                    &:hover {
                        color: #999;
                        background-color: #222c69;
                    }

                }
            }
        }
    }
}

.section.about.doc {
    background-image: url(../bg.jpg);
    background-color: #08142c;
    background-blend-mode: saturation;
}

.section.login {
    background-image: url('../bg.jpg');
    background-color: rgba(3, 0, 51, 0.749);
    background-blend-mode: multiply;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;

    .login_card {
        height: 600px;
        width: 400px;
        background: #00000046;
        backdrop-filter: blur(45px);
        border: 1px solid #ffffff32;
        border-radius: 25px;
        padding: 25px;
        color: wheat;

        h2.login_head {
            line-height: calc(20/100 * 600px);
            text-align: center;
            font-size: 35px;
            letter-spacing: 5px;
            border-bottom: 1px solid #ffffff32;
        }

        #login_form {
            margin: 15px 0;

            label {
                display: block;
                font-size: 25px;
                line-height: 45px;
                margin-bottom: 20px;
            }

            input[type="text"],
            input[type="password"] {
                height: 40px;
                font-size: 20px;
                width: 100%;
                background: #ffffff13;
                border-top: 0;
                border-left: 0;
                border-right: 0;
                border-bottom: 5px solid #ffffff32;
                outline: none;
                transition: .5s;
                margin-bottom: 30px;
                color: white;
                border-radius: 8px 8px 0 0;
                padding: 0 15px;

                &:focus {
                    border-bottom: 5px solid #ffffff72;
                }
            }

            .remd {
                display: flex;
                align-items: center;

                input {
                    display: inline;
                }

                label {
                    font-size: 18px;
                    margin-bottom: 0;
                    margin-left: 15px;
                }
            }

            button {
                border: none;
                margin: none;
                height: 40px;
                width: 100%;
                font-size: 18px;
                background: #08124c;
                color: white;
                transition: .2s;

                &:hover {
                    background-color: cornflowerblue;
                    color: #08142c;
                    font-weight: 900;
                }
            }

        }
    }

}

.section.tut {
    .tutcontent {
        min-height: calc(calc(100vh - 50px) - 20vh);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .content {
        display: flex;
        justify-content: space-around;
        align-items: center;

        .plintro,
        .codeDIsplaytut {
            width: 40%;
        }

        .codeDIsplaytut {
            width: 60%;
        }

        .plintro p {
            .head_links {
                height: 5vh;
                overflow: hidden;
                display: flex;
                justify-content: space-evenly;
                align-items: center;

                .headlinks {
                    transition: .2s;
                    display: inline-block;
                    padding: 15px 25px;
                    font-size: 18px;
                    text-decoration: none;
                    color: #000;
                    background: cornsilk;

                    &:hover {
                        background: rgb(205, 181, 135);
                        color: #030;
                        box-shadow: 0 0 15px #0000006d,
                            0 0 25px #ffffff72;
                    }

                }
            }
        }
    }
}

.section.about.doc.editor_holder {
    .content {
        min-height: 70vh;
        padding: 0;

        .editor_area {
            display: flex;
            align-items: center;
            justify-content: space-between;
            min-height: 70vh;
            padding: 0 2vw;

            .editor_text {
                min-height: 65vh;
                width: 48%;
                background: #444;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                .lable {
                    height: 30px;
                    font-size: 25px;
                    color: #888;
                    font-weight: bolder;
                    overflow-y: hidden;
                }

                textarea {
                    min-height: 65vh;
                    width: 100%;
                    resize: none;
                    outline: none;
                    border: none;
                    background: #222;
                    color: #21eb21;
                    padding: 15px;
                    font-size: 20px;
                    border: 1px solid rgba(255, 255, 255, .2);
                    font-family: 'source code pro', consolas;
                }

            }

            .output {
                min-height: 65vh;
                width: 48%;
                display: flex;
                align-items: center;
                justify-content: center;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                background: #444;

                .lable {
                    height: 30px;
                    font-size: 25px;
                    color: #888;
                    font-weight: bolder;
                    overflow-y: hidden;
                }

                iframe {
                    border: 1px solid #333;
                    min-height: 65vh;
                    width: 100%;
                    outline: none;
                    border: none;
                    background: #ffffff;
                }
            }

        }
    }
}


@media screen and (max-width : 850px) {
    .nav {

        .login_link_nav {
            display: none;
        }
    }

    .sidebar {
        width: 50vw;
        z-index: 1000;
        top: 0;
        left: -50vw;
        transition: .4s linear;

        .sideLogo {
            height: 25%;

            &::before {
                $he: 15vh;
                top: calc(50% - $he/2);
                left: calc(50% - $he/2);
                height: $he;
                width: $he;
            }

        }

        .sideNavs {
            height: calc(75 / 100 * 100vh);

            .Sidenav {
                height: 18;
            }

        }

    }

    section {
        left: 0;
        width: 100vw;

        .section {
            width: 100vw;
        }
    }

    .section.s_h {
        .Shower {
            flex-direction: column;
            justify-content: space-around;
            width: 90%;

            .head,
            .para {
                width: 80%;
                text-align: center;
            }

            .para {
                text-align: center;
                font-size: 18px;
                transform: translateY(-10vh);

                .head_links {
                    .headlinks {
                        font-size: 15px;
                        padding: 12px 18px;
                    }
                }

            }
        }
    }

    #footer {

        h1,
        p,
        .social_media {
            width: 100%;
        }
    }

    .section.about {
        .component_head.long {
            font-size: 25px;
            line-height: 5vh;
            padding: 2vh;
        }
    }

    .section.tut {
        .content {
            display: block;
            // background: #000;
            flex-direction: column;

            .plintro {
                width: 100%;
                text-align: center;
                margin-bottom: 50px;
            }
        }
    }

    .section.about.doc.editor_holder {
        .content {
            .editor_area {
                flex-direction: column;

                .editor_text,
                .output {
                    width: 99%;
                    margin: 10px;
                }
            }
        }
    }
}

@media (max-width: 700px) {
    .nav {
        padding: 0 5px;
    }
}

@media (max-width: 500px) {
    .section.login {
        .login_card {
            height: 70vh;
            width: 85%;
            overflow-y: none;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }

    .sidebar {
        top: 50px;
        width: 100vw;
        height: calc(100vh - 50px);
        transition: .2s linear;

        .sideNavs {
            height: calc(75 / 100 * calc(100vh - 50px));
        }
    }

    @media (max-height: 750px) {
        .section.login {
            .login_card {
                height: 90vh;
            }
        }

    }
}